/* eslint-disable global-require */
import React from 'react';
import {
  Card, CardContent, CardMedia, Container, Grid, Typography, makeStyles,
} from '@material-ui/core/';
import { Link } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  cardGrid: {
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8),
  },
  card: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
  },
  cardMedia: {
    paddingTop: '56.25%', // 16:9
  },
  cardContent: {
    flexGrow: 1,
  },
}));

const items = [
  {
    index: 0,
    name: '',
    path: '/ISort',
    img: require('./CardImg/insertion_sort.png'),
    desc: '          ,'
    + '      .',
  },
  {
    index: 1,
    name: '',
    path: '/SSort',
    img: require('./CardImg/selection_sort.png'),
    desc: '    .       .'
    + '        .',
  },
  {
    index: 2,
    name: '',
    path: '/BSort',
    img: require('./CardImg/bubble_sort.png'),
    desc: '     .'
    + '          .',
  },
  {
    index: 3,
    name: '',
    path: '/MSort',
    img: require('./CardImg/merge_sort.png'),
    desc: '   .'
    + '        .',
  },
  {
    index: 4,
    name: '',
    path: '/QSort',
    img: require('./CardImg/quick_sort.png'),
    desc: '         .'
    + '     ,     .',
  },
  {
    index: 5,
    name: '',
    path: '/HSort',
    img: require('./CardImg/heap_sort.png'),
    desc: ',           .'
    + ',     pop   .',
  },
  {
    index: 6,
    name: '  ',
    path: '/bfs',
    img: require('./CardImg/bfs.png'),
    desc: '              .'
    + '               .',
  },
  {
    index: 7,
    name: '  ',
    path: '/dfs',
    img: require('./CardImg/dfs.png'),
    desc: '       ,       .'
    + '            .',
  },
  {
    index: 8,
    name: '    ',
    path: '/prim',
    img: require('./CardImg/prim.png'),
    desc: '          '
    + '        ,      .',
  },
  {
    index: 9,
    name: '    ',
    path: '/kruskal',
    img: require('./CardImg/kruskal.png'),
    desc: '           .'
    + '      UNION-FIND   .',
  },
  {
    index: 10,
    name: '   ',
    path: '/dijkstra',
    img: require('./CardImg/dijkstra.png'),
    desc: '               .'
    + '            .',
  },
];

function Cards(): JSX.Element {
  const classes = useStyles();
  return (
    <Container className={classes.cardGrid} maxWidth="md">
      <Grid container spacing={4}>
        {items.map((item) => (
          <Grid item key={item.index} xs={12} sm={6} md={4}>
            <Link to={item.path} style={{ textDecoration: 'none' }}>
              <Card className={classes.card}>
                <CardMedia
                  className={classes.cardMedia}
                  image={item.img}
                  title="Image title"
                />
                <CardContent className={classes.cardContent}>
                  <Typography gutterBottom variant="h5" component="h2">
                    {item.name}
                  </Typography>
                  <Typography>
                    {item.desc}
                  </Typography>
                </CardContent>
              </Card>
            </Link>
          </Grid>
        ))}
      </Grid>
    </Container>
  );
}

export default Cards;
