import React, { useState, useEffect } from 'react';

import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import PauseIcon from '@material-ui/icons/Pause';
import SkipPreviousIcon from '@material-ui/icons/SkipPrevious';
import SkipNextIcon from '@material-ui/icons/SkipNext';
import SortBars from './SortBars';
import { GraphBar, Process } from '../../util';
import { makeRandomList, sort, rendering } from './UtilFunction';


const useStyles = makeStyles((theme) => ({
  button: {
    margin: theme.spacing(1),
  },
}));
// speed playing             .
let playing = false;
let speed = 1000;

//        .
let wholeSortProcess: Process[];
//       .
let processLength: number;
let initialization = false;

// eslint-disable-next-line @typescript-eslint/no-explicit-any
function SortView(info: any): JSX.Element {
  console.log(initialization);
  const pathName = info.location.pathname.substr(1);
  const classes = useStyles();
  const [toggle, setToggle] = useState(false);
  //   
  const [graphBars, setBar] = useState<GraphBar[]>(makeRandomList());

  if (graphBars !== undefined) {
    if (initialization) {
      wholeSortProcess = sort(graphBars, pathName);
      processLength = wholeSortProcess.length;
    }
  }

  const [nowDepth, setNowDepth] = useState<number>(0);
  const [nowPlaying, setNowPlaying] = useState<boolean>(playing);
  //        [, , , ]   .
  function setDepth(depth: number): void {
    if (depth <= processLength - 1) {
      setNowDepth(depth);
    }
  }
  //      .
  function goTo(depth: number): void {
    initialization = false;
    // stop  .
    if (playing) {
      //     depth  .
      if (depth < processLength && depth > -1) {
        //  depth .
        setDepth(depth);
        setBar(rendering(graphBars, wholeSortProcess[depth]));
      }
    }
  }

  //    .      .
  function makeRandomNumber(howSorted = 'random'): void {
    const temp: GraphBar[] = makeRandomList(howSorted);
    setBar(temp);
    setDepth(0);
    initialization = true;
  }

  //  .
  function stop(): void {
    playing = false;
    setNowPlaying(playing);
    initialization = false;
  }

  //  flag  .(    )
  function play(): void {
    if (processLength === undefined) {
      wholeSortProcess = sort(graphBars, pathName);
      processLength = wholeSortProcess.length;
    }
    playing = true;
    setNowPlaying(playing);
  }
  //   goTo()   .
  const flow = (depth: number): void => {
    // stop   .
    if (playing) {
      goTo(depth);
      //  .
      if (depth < processLength - 1) {
        // setTimeout()  speed    flow .
        setTimeout(() => {
          flow(depth + 1);
        }, speed);
      } else {
        //    .
        stop();
      }
    }
  };

  useEffect(() => () => {
    function init(): void {
      playing = false;
      setNowPlaying(playing);
      initialization = false;
    }
    init();
  }, []);

  //     
  if (!playing) {
    initialization = true;
  }

  return (
    <div style={{
      height: window.innerHeight,
    }}
    >
      <div style={{
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'center',
      }}
      >
        {/*    stop    == (if (!playing)) */}
        <Button className={classes.button} size="medium" onClick={(): void => { if (!playing) { play(); goTo(0); stop(); initialization = true; } }}> </Button>
        <IconButton aria-label="skipPrevious" onClick={(): void => { if (!playing) { play(); goTo(nowDepth - 1); stop(); } }}>
          <SkipPreviousIcon />
        </IconButton>
        <IconButton aria-label="playAndPause" onClick={(): void => { if (!playing) { play(); flow(nowDepth); initialization = true; } else { stop(); initialization = true; } }}>
          {(!nowPlaying || initialization)
            && <PlayArrowIcon />}
          {(nowPlaying && !initialization)
            && <PauseIcon />}
        </IconButton>
        <IconButton aria-label="skipNext" onClick={(): void => { if (!playing) { play(); goTo(nowDepth + 1); stop(); } }}>
          <SkipNextIcon />
        </IconButton>
        {!toggle && <Button className={classes.button} size="medium" onClick={(): void => { if (!toggle) { setToggle(true); } else { setToggle(false); } }}></Button>}
        {toggle
          && (
            <div>
              <Button className={classes.button} color="primary" size="medium" onClick={(): void => { makeRandomNumber(); stop(); }}></Button>
              <Button className={classes.button} color="primary" size="medium" onClick={(): void => { makeRandomNumber('increasing'); stop(); }}></Button>
              <Button className={classes.button} color="primary" size="medium" onClick={(): void => { makeRandomNumber('decreasing'); stop(); }}></Button>
              <Button className={classes.button} color="primary" size="medium" onClick={(): void => { makeRandomNumber('nearlyIncreasing'); stop(); }}></Button>
              <Button className={classes.button} color="primary" size="medium" onClick={(): void => { makeRandomNumber('nearlyDecreasing'); stop(); }}></Button>
            </div>
          )}
        <Button className={classes.button} size="medium" onClick={(): void => { if (speed < 2000) { speed += 100; } }}></Button>
        <Button className={classes.button} size="medium" onClick={(): void => { if (speed > 100) { speed -= 100; } }}></Button>
      </div>

      <div style={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'flex-end',
      }}
      >
        <SortBars graphBars={graphBars} />
      </div>
    </div>
  );
}

export default SortView;
