import React, { useState, useEffect } from 'react';

import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import PauseIcon from '@material-ui/icons/Pause';
import SkipPreviousIcon from '@material-ui/icons/SkipPrevious';
import SkipNextIcon from '@material-ui/icons/SkipNext';

import GraphNodes from './GraphNodes';
import { Node, Line, GProcess } from '../../util';
import { makeGraph, rendering, search } from './UtilFunction';


const useStyles = makeStyles((theme) => ({
  button: {
    margin: theme.spacing(1),
  },
}));
// speed playing             .
let playing = false;
let speed = 1000;

//        .
let wholeSearchProcess: GProcess[];
//       .
let processLength: number;
let initialization = false;

// eslint-disable-next-line @typescript-eslint/no-explicit-any
function GraphView(info: any): JSX.Element {
  const pathName = info.location.pathname.substr(1);
  const classes = useStyles();
  const [toggle, setToggle] = useState(false);
  const data = makeGraph('normal');
  const [graphNodes, setGraphNodes] = useState<Node[]>(data[0]);
  const [graphLines, setGraphLines] = useState<Map<string, Line>>(data[1]);
  const [list, setList] = useState<number[]>([]);

  if (graphNodes !== undefined) {
    if (initialization) {
      wholeSearchProcess = search(graphNodes, pathName);
      processLength = wholeSearchProcess.length;
    }
  }

  const [nowDepth, setNowDepth] = useState<number>(0);
  const [nowPlaying, setNowPlaying] = useState<boolean>(playing);
  //        [, , , ]   .
  function setDepth(depth: number): void {
    if (depth <= processLength - 1) {
      setNowDepth(depth);
    }
  }
  //      .
  function goTo(depth: number): void {
    initialization = false;
    // stop  .
    if (playing) {
      //     depth  .
      if (depth < processLength && depth > -1) {
        //  depth .
        setDepth(depth);
        const temp = rendering(graphNodes, graphLines, wholeSearchProcess[depth]);
        setGraphNodes(temp[0]);
        setGraphLines((temp[1]));
        setList(wholeSearchProcess[depth].list);
      }
    }
  }

  //    .      .
  function makeRandomNumber(density = 'normal'): void {
    const temp = makeGraph(density);
    setGraphNodes(temp[0]);
    setGraphLines(temp[1]);
    setDepth(0);
    initialization = true;
  }

  //  .
  function stop(): void {
    playing = false;
    setNowPlaying(playing);
    initialization = false;
  }

  //  flag  .(    )
  function play(): void {
    if (processLength === undefined) {
      wholeSearchProcess = search(graphNodes, 'bfs');
      processLength = wholeSearchProcess.length;
    }
    playing = true;
    setNowPlaying(playing);
  }
  //   goTo()   .
  const flow = (depth: number): void => {
    // stop   .
    if (playing) {
      goTo(depth);
      //  .
      if (depth < processLength - 1) {
        // setTimeout()  speed    flow .
        setTimeout(() => {
          flow(depth + 1);
        }, speed);
      } else {
        //    .
        stop();
      }
    }
  };

  useEffect(() => (): void => {
    function init(): void {
      playing = false;
      setNowPlaying(playing);
      initialization = false;
    }
    init();
  }, []);

  //     
  if (!playing) {
    initialization = true;
  }
  return (
    <div
      style={{
        height: '700px',
      }}
    >
      <div style={{
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'center',
      }}
      >
        {/*    stop    == (if (!playing)) */}
        <Button className={classes.button} size="medium" onClick={(): void => { if (!playing) { play(); goTo(0); stop(); initialization = true; } }}> </Button>
        <IconButton aria-label="skipPrevious" onClick={(): void => { if (!playing) { play(); goTo(nowDepth - 1); stop(); } }}>
          <SkipPreviousIcon />
        </IconButton>
        <IconButton aria-label="playAndPause" onClick={(): void => { if (!playing) { play(); flow(nowDepth); initialization = true; } else { stop(); initialization = true; } }}>
          {(!nowPlaying || initialization)
            && <PlayArrowIcon />}
          {(nowPlaying && !initialization)
            && <PauseIcon />}
        </IconButton>
        <IconButton aria-label="skipNext" onClick={(): void => { if (!playing) { play(); goTo(nowDepth + 1); stop(); } }}>
          <SkipNextIcon />
        </IconButton>
        {!toggle && <Button className={classes.button} size="medium" onClick={(): void => { if (!toggle) { setToggle(true); } else { setToggle(false); } }}></Button>}
        {toggle
          && (
            <div>
              <Button className={classes.button} color="primary" size="medium" onClick={(): void => { makeRandomNumber('low'); stop(); }}></Button>
              <Button className={classes.button} color="primary" size="medium" onClick={(): void => { makeRandomNumber('normal'); stop(); }}></Button>
              <Button className={classes.button} color="primary" size="medium" onClick={(): void => { makeRandomNumber('high'); stop(); }}></Button>
            </div>
          )}
        <Button className={classes.button} size="medium" onClick={(): void => { if (speed < 2000) { speed += 100; } }}></Button>
        <Button className={classes.button} size="medium" onClick={(): void => { if (speed > 100) { speed -= 100; } }}></Button>
      </div>
      <div style={{
        display: 'flex',
        justifyContent: 'center',
      }}
      >
        <GraphNodes
          graphNodes={graphNodes}
          graphLines={Array.from(graphLines.values())}
          list={list}
        />
      </div>
    </div>
  );
}
export default GraphView;
